<template>
    <view class="background">

		<view class="box" v-for="item in listArr" :key="item.seminarBookId">
			<!-- Item {{ index + 1 }}: {{ item }} -->
			<view class="stu">预约人：{{item.seminarUserId}} </view>
			<view class="room">预约教室：{{item.seminarRoomNumber}} </view>
			<view class="stu">预约日期：{{item.seminarDate}} </view>
			<view class="time">预约时间起：{{item.startTime}} </view>
            <view class="time2">预约时间止：{{item.endTime}} </view>
			<button  @click="detele(item.userName , item.seminarRoomNumber , item.seminarDate)" class="button">取消预约</button>
		</view>
	</view>
</template>


<script>
	export default {
	    data(){
	        return{
	            listArr:[],
				userName:"",
				//index:0,
				SeminarDate:"",
				seminarRoomNumber:""
	        };
	    },
		methods: {
	        getData(){
	            uni.request({
	                url:"http://8.134.201.230:8082/seminar/appointments/"+this.userName,
					 //url:"http://8.134.201.230:8082/seminar/appointments/20yjma",
					method: 'GET',
	                success:res=>{
	                    console.log(res)
	                    this.listArr=res.data.data
	                }
	            })
             },   
		    detele(item1 , item2 , item3) {
		        uni.showModal({
		        	title: '提示',
		        	content: '你确定要删除吗',
		        	success: function (res) {
		        		if (res.confirm) {
		        			console.log('确定');
                            uni.request({
                               // url:"http://8.134.201.230:8082/seminar/appointments/delete/"+"username="this.userName+"&room="+this.seminarRoomNumber+"&date="+this.SeminarDate,
							   url: "http://8.134.201.230:8082/seminar/appointments/delete/username=" + item1 + "&room=" + item2 + "&date=" + item3,

								 method: 'GET',
                            });                              
                            uni.navigateTo({
                            	url: '/pages/mybook/mybook'
                            });
		        		} else if (res.cancel) {
		        			console.log('取消');
		        		}
		        	}
		        });
		    }
		},
        onLoad() {
			this.userName=getApp().globalData.username;
			console.log(this.userName)
            this.getData();
        },
		mounted(){
			console.log(this.$router);
			//this.userName=getApp().globalData.username;
			//console.log(this.userName)
		}
	}
</script>

<style>
	.background {
		background-color: #ffffff;
	}
	.box{
		color: white;
		width: 260px;
		height: 170px;
		background-color: #83c6c2;
		margin: auto;
		margin-top:20px;
		margin-bottom: 20px;
		text-align: center;
		line-height: 70rpx;
		font-size:10px;
	}
	.button{
		height:50rpx;
		width: 150rpx;
		color: black;
		font-size: 20rpx;
	}
</style>